@import "../app/mixins.scss";
.studio {
  position: relative;
  display: flex;

  &__img {
    display: block;
    width: 100%;
  }
  &__content {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }
}

.studio-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title {
    @include font6;
  }
  &__subt {
    width: 530px;
    font-size: 20px;
    letter-spacing: 0.02em;
    text-align: center;
  }
}

.studio-info {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    @include font7;
    text-align: center;
  }
  &__subt {
    text-align: center;
    width: 219px;
    @include font8;
  }
}

@media screen and (max-width: 767px) {
  .studio {
    &__img {
      height: 100vh;
      object-fit: cover;
    }
    &__content {
      padding: 10px;
    }
  }
  .studio-header {
    &__title {
      font-size: 42px;
    }
    &__subt {
      font-size: 16px;
      width: 100%;
    }
  }

  .studio-info {
    &__title {
      font-size: 25px;
    }
    &__subt {
      font-size: 23px;
    }
  }
}
