@import "../app/mixins.scss";

.plans {
  @include backColorDark;
  margin-top: 90px;
  color: #fff;
  display: flex;
  flex-direction: column;
  &__title {
    text-align: center;
  }
  &__wrapper {
    display: flex;
  }
}

.plans-card {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;

  &__item {
    position: relative;
    width: 294px;
    margin: 15px;
  }
  &__item:nth-child(2) {
    .plans-card__header {
      background-color: rgba(255, 255, 255, 0.16);
    }
    .plans-card__best-value {
      @include backColorOrange;
      font-size: 14px;
      padding: 5px 10px;
      position: absolute;
      top: -15px;
    }
    .plans-card__footer {
      background-color: rgba(145, 145, 145, 0.16);
    }
    .plans-card__footer:nth-child(5) {
      display: none;
    }
  }
  &__header {
    padding: 30px;
    border-bottom: 1px solid #7d7c7c7a;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid#7d7c7c7a;
  }

  &__footer {
    @include font11;
    font-size: 18px;
    padding: 30px;
    height: 268px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid#7d7c7c7a;
  }

  &__title {
    @include font8;
  }
  &__price {
    display: flex;
  }
  &__sum {
    @include font9;
  }
  &__info {
    height: 92px;
    text-align: center;
  }
  &__period {
    height: 17px;
    @include font10;
  }
  &__class {
    margin-top: 12px;
    height: 40px;
    @include font11;
  }
  &__valid {
    height: 17px;
    color: #a0a0a0;
    @include font10;
  }
  &__link {
    margin-top: 24px;
    width: 100%;
    text-align: center;
    text-decoration: none;
    padding: 15px 0;
    @include backColorOrange;
  }
  &__privilege {
    padding: 9px 0;
  }
  &__btn {
    visibility: hidden;
    background-color: transparent;
    color: #a0a0a0;
    position: absolute;
    bottom: 0;
    outline: none;
  }
}

@media screen and (max-width: 767px) {
  .plans {
    margin: 0;
  }

  .plans-card {
    &__item {
      width: 100%;
    }
    &__item:nth-child(1) {
      .plans-card__footer {
        .plans-card__privilege:nth-child(4) {
          display: none;
        }
      }

      .plans-card__footer {
        .plans-card__privilege:nth-child(4) {
          display: none;
        }
      }
    }
    &__item:nth-child(2) {
      order: -1;
      .plans-card__footer {
        .plans-card__privilege:nth-child(4) {
          display: none;
        }
      }
    }
    &__sum {
      font-size: 44px;
    }
    &__footer {
      padding: 0;
      max-height: 0;
      overflow: hidden;
      transition: all 0.2s;
      &.active {
        max-height: 100%;
        overflow: visible;
      }
    }
    &__btn {
      visibility: visible;
    }
  }
}
