@import "../app/mixins.scss";
.footer {
  background-color: #fe6148;
  height: 500px;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
}

.footer-contacts {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  color: #252128;
  &__title {
    @include font7;
    font-weight: normal;
    margin-bottom: 58px;
    margin-top: 68px;
  }
  &__txt {
    @include font8;
    font-weight: normal;
    margin-bottom: 20px;
  }
  &__img {
    width: 32px;
    height: 32px;
    display: block;
  }
  &__links {
    display: flex;
  }
  &__link:nth-child(2) {
    margin: 0 12px;
  }
}

.footer-form {
  display: flex;
  flex-direction: column;

  &__aside {
    display: flex;
    justify-content: space-between;
    margin-top: 25px;
  }
  &__link {
    cursor: pointer;
    background-color: transparent;
    outline: none;
  }
}

.form {
  display: flex;
  flex-direction: column;
  &__element {
    width: 100%;
  }
  &__input {
    border: 2px solid black;
    background-color: #fe6148;
    color: black;
    width: 431px;
    margin-bottom: 10px;
    height: 38px;
    padding: 3px 3px 3px 12px;
    outline: none;
  }
  &__input:focus {
    border: 1px solid black;
  }
  &__input_error {
    background-color: #fe6148;
    color: black;
    width: 431px;
    margin-bottom: 10px;
    height: 38px;
    padding: 3px 3px 3px 12px;
    border: 1px solid red;
    outline: none;
  }
  &__input_error:focus {
    border: 1px solid black;
  }
  &__input_massage {
    height: 115px;
    padding: 15px 10px 3px 16px;
  }
  &__btn {
    width: 142px;
    height: 38px;
    background-color: #252128;
    color: #fe6148;
    align-self: flex-end;
  }
}

@media screen and (max-width: 767px) {
  .footer {
    flex-direction: column;
    align-items: inherit;
    height: 802px;
    padding: 15px;
  }
  .form {
    align-items: center;
    &__input {
      width: 100%;
    }
  }
  .footer-contacts {
    margin-bottom: 20px;
  }
}
